AAA,CACA,gBAAA,CAGF,4BACE,YAAA,CACA,qBAAA,CACA,sBAAA,CACA,QAAA,CAGF,+BACE,cAAA,CACA,mBAAA,CAGF,8BACE,mBAAA,CAGF,qBACE,UAAA,CACA,WAAA,CACA,WAAA,CACA,eAAA,CACA,YAAA,CAGF,yBACE,UAAA,CACA,WAAA,CAIF,0BACE,YAAA,CACA,sBAAA,CACA,iBAAA,CACA,UAAA,CACA,gBAAA,CACA,kBAAA,CACA,eAAA,CAEF,oDACE,6BAAA,CACA,oBAAA,CACA,SAAA,CACA,UAAA,CACA,uBAAA,CAEF,2DACE,6BAAA,CAAA","sourcesContent":["/* ===== Fonts ===== */\r\n@font-face {\r\n  font-family: 'PT Sans';\r\n  src: url('../assets/fonts/ptsans-regular.woff2') format('woff2');\r\n  font-weight: 400;\r\n  font-style: normal;\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: 'PT Sans';\r\n  src: url('../assets/fonts/ptsans-bold.woff2') format('woff2');\r\n  font-weight: 700;\r\n  font-style: normal;\r\n  font-display: swap;\r\n}\r\n\r\n/* ===== CSS Variables ===== */\r\n:root {\r\n  --c-blue-strong: #5d5fef;\r\n  --c-blue: #3877ee;\r\n  --c-pink: #ef5da8;\r\n  --c-text: #42567a;\r\n  --c-bg: #f4f5f9;\r\n  --c-stroke: #303e58;\r\n\r\n  /* вертикальный градиент для уса заголовка */\r\n  --g-accent-vert: linear-gradient(180deg, #3877ee 0%, #ef5da8 100%);\r\n\r\n  /* размеры круга (должны совпадать с TSX) */\r\n  --orbit-w: 536px;\r\n  --orbit-h: 530px;\r\n\r\n  /* контейнерные отступы (ПК: слева 320, справа 160) */\r\n  --container-pad-left: clamp(16px, 8vw, 320px);\r\n  --container-pad-right: clamp(16px, 4vw, 160px);\r\n}\r\n\r\n/* ===== Base ===== */\r\n* {\r\n  box-sizing: border-box;\r\n}\r\n\r\nhtml,\r\nbody,\r\n#root {\r\n  height: 100%;\r\n}\r\n\r\nbody {\r\n  margin: 0;\r\n  font-family: 'PT Sans', system-ui, -apple-system, Segoe UI, Roboto, sans-serif;\r\n  color: var(--c-text);\r\n  background: #fff;\r\n}\r\n\r\n.container {\r\n  padding-left: var(--container-pad-left);\r\n  padding-right: var(--container-pad-right);\r\n}\r\n\r\n/* ===== Historical Dates ===== */\r\n\r\n.hdates {\r\n  position: relative;\r\n  padding: 20px 0 clamp(24px, 6vw, 70px) 0;\r\n}\r\n\r\n.hdates-mobile-footer, .hdates__mob-range {\r\n  display: none;\r\n}\r\n\r\n\r\n.hdates__stage {\r\n  position: relative;\r\n  height: var(--orbit-h);\r\n}\r\n\r\n/* на мобиле сцена не нужна фиксированной высоты */\r\n@media (max-width: 767px) {\r\n  .hdates__stage { height: auto; }\r\n}\r\n\r\n/* сеточные линии: центральные + боковые */\r\n.hdates__grid-line {\r\n  position: absolute;\r\n  inset: 0 auto 0 50%;\r\n  width: 1px;\r\n  background: rgba(48, 62, 88, 0.2);\r\n  transform: translateX(-50%);\r\n}\r\n\r\n.hdates__grid-line--h {\r\n  inset: 50% 0 auto 0;\r\n  height: 1px;\r\n  width: 100%;\r\n  transform: translateY(-50%);\r\n}\r\n\r\n.hdates__grid-line--left {\r\n  inset: 0 auto 0 0;\r\n  transform: none;\r\n}\r\n\r\n.hdates__grid-line--right {\r\n  inset: 0 0 0 auto;\r\n  left: auto;\r\n  transform: none;\r\n}\r\n\r\n/* Заголовок */\r\n.hdates__title {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 46px;\r\n  margin: 0 0 24px 0;\r\n\r\n  font-weight: 700;\r\n  font-size: clamp(20px, 4vw, 56px);\r\n  line-height: 1.2;\r\n  color: var(--c-text);\r\n}\r\n\r\n.hdates__title-bar {\r\n  width: 4px;\r\n  height: 2em;\r\n  background: var(--g-accent-vert);\r\n}\r\n\r\n/* Большие числа — абсолютом по центру секции поверх круга */\r\n.hdates__years {\r\n  position: absolute;\r\n  left: 50%;\r\n  top: calc(50% - var(--orbit-h) / 8 - 25px);\r\n  transform: translateX(-50%);\r\n  z-index: -1;\r\n\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: clamp(16px, 4vw, 48px);\r\n  margin: 0;\r\n  pointer-events: none;\r\n}\r\n\r\n.hdates__year {\r\n  font-weight: 700;\r\n  font-size: clamp(56px, 12vw, 200px);\r\n  line-height: 1.6;\r\n  white-space: nowrap;\r\n  user-select: none;\r\n}\r\n\r\n.hdates__year--start { color: var(--c-blue-strong); }\r\n.hdates__year--end   { color: var(--c-pink); }\r\n\r\n/* Круг */\r\n.hdates__orbit {\r\n  position: absolute;\r\n  left: 50%;\r\n  top: 64%;\r\n  width: var(--orbit-w);\r\n  height: var(--orbit-h);\r\n  border-radius: 50%;\r\n  z-index: 100;\r\n  border: 1px solid rgba(48, 62, 88, 0.2);\r\n  transform: translate(-50%, -50%);\r\n  transform-origin: 50% 50%;\r\n}\r\n\r\n/* На мобиле круг/линии/градиент скрываем */\r\n@media (max-width: 767px) {\r\n  .hdates__grid-line,\r\n  .hdates__title-bar,\r\n  .hdates__orbit {\r\n    display: none;\r\n  }\r\n\r\n  .hdates__years {\r\n    position: static;\r\n    transform: none;\r\n    margin: clamp(8px, 3vw, 24px) 0;\r\n    pointer-events: auto;\r\n  }\r\n}\r\n\r\n/* Точки */\r\n.hdates__dot {\r\n  position: absolute;\r\n  width: 6px;\r\n  height: 6px;\r\n  border-radius: 50%;\r\n  background: var(--c-stroke);\r\n  transform: translate(-50%, -50%);\r\n  border: none;\r\n  padding: 0;\r\n  cursor: pointer;\r\n\r\n  /* большой hitbox */\r\n  &::before {\r\n    content: \"\";\r\n    position: absolute;\r\n    left: 50%;\r\n    top: 50%;\r\n    width: 44px;\r\n    height: 44px;\r\n    transform: translate(-50%, -50%);\r\n    border-radius: 50%;\r\n    background: transparent;\r\n  }\r\n\r\n  &:focus-visible::before {\r\n    outline: 2px solid rgba(56, 119, 238, .5);\r\n    outline-offset: 0;\r\n  }\r\n}\r\n\r\n@media (max-width: 767px) {\r\n  .hdates__dot::before { width: 56px; height: 56px; }\r\n}\r\n\r\n/* плавающие лейблы */\r\n.hdates__float-label {\r\n  position: absolute;\r\n  left: 0;  // выставляет JS\r\n  top: 0;   // выставляет JS\r\n  transform: translate(0, -50%); // СПРАВА от точки\r\n  white-space: nowrap;\r\n  font-size: 20px;\r\n  color: var(--c-text);\r\n  line-height: 1;\r\n  pointer-events: none;\r\n  opacity: 0;\r\n  transition: opacity .2s ease;\r\n  z-index: 150;\r\n}\r\n\r\n@media (max-width: 767px) {\r\n  .hdates__float-label { display: none; }\r\n}\r\n\r\n/* пузырёк с номером */\r\n.hdates__dot-bubble {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  width: 56px;\r\n  height: 56px;\r\n  border-radius: 50%;\r\n  background: var(--c-bg);\r\n  border: 1px solid rgba(48, 62, 88, 0.5);\r\n  display: grid;\r\n  place-items: center;\r\n  